            showList() {
                if (typeof this.Jurisdiction['goods_view'] == 'boolean') {
                    return this.list.filter(item => {
                        if (item.text == '商品') {
                            return this.Jurisdiction['goods_view'];
                        } else if (item.text == '订单') {
                            return this.Jurisdiction['order_view'];
                        }
                        return true
                    })
                } else {
